import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import { ParseProvider } from '../../providers/parse/parse';

@IonicPage({
  name: 'input-field'
})
@Component({
  selector: 'page-input-field',
  templateUrl: 'input-field.html',
})
export class InputFieldPage {

  bank;

  agreement: boolean;

  data = {
    nama: '',
    perusahaan: '',
    jabatan: '',
    noHp: '',
    noTelepon: '',
    jumlahKaryawanTetap: '',
    karyawanLepas: '',
    rataGaji: '',
    rencanaRataPlafon: '',
    email: '',
    agreement: this.agreement,
    bank: this.bank
  }

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              private toastCtrl: ToastController,
              private loadingCtrl: LoadingController,
              private parse: ParseProvider) {

                this.bank = navParams.get('bank');

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InputFieldPage');
  }

  back() {
    this.navCtrl.pop()
  }

  send() {
    let loading = this.loadingCtrl.create({
      content: 'Harap tunggu...'
    });
    loading.present();

    let toast = this.toastCtrl.create({
      message: 'Data Anda Telah Terkirim',
      duration: 2000,
      position: 'buttom'
    });   

    this.parse.pushData(this.data).then((res) => {
      console.log(res)
      loading.dismiss();
      this.navCtrl.popTo(this.navCtrl.getByIndex(0));
      toast.present();
    }, (err) => {
      console.log(err)
      loading.dismiss();
    })
  }

  onSelectChange(ev) {
    this.agreement = ev.value;
    this.data.agreement = ev.value;
  }

}



